import React, { Component } from 'react';
import { Form, Icon, Input, Button } from 'antd';
import { connect } from 'react-redux';
import { registerPostData } from './../../../redux/actions/register';
import { loginPostData } from './../../../redux/actions/authentication';
import { withRouter, Redirect } from 'react-router-dom';
import './../../../styles/register.less';
import { shallowEqual } from 'shouldcomponentupdate-children';

const FormItem = Form.Item;

class Register extends Component {
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				this.setState({ userName: values.email, password: values.password });
				console.log('Received values of form: ', values);
				this.props.registerPostData(values);
			}
		});
	};

	navigateLogin = () => {
		this.props.history.push('/login');
	};

	shouldComponentUpdate(nextProps, nextState) {
		return shallowEqual(this.props, nextProps, this.state, nextState);
	}

	render() {
		if (this.props.success) {
			const values = {
				userName: this.state.userName,
				password: this.state.password
			};

			this.props.loginPostData(values);
		}

		var redirect = '';

		if (this.props.authorized) {
			redirect = (
				<Redirect
					to={{
						pathname: '/dashboard'
					}}
				/>
			);
		}

		const { getFieldDecorator } = this.props.form;

		return (
			<div className="login-container register">
				{redirect}
				<Form style={{ gridRow: '2/2', gridColumn: '2/2'}} onSubmit={this.handleSubmit} className="login-form">
					<div className="login-logo-container">
						<Icon className="logo-icon-container" type="user" />
						<span className="logo-title-container">S.E.C.A.</span>
					</div>
					<div className="inputs-container reset-inputs-container">
						<FormItem hasFeedback className="form-item">
							{getFieldDecorator('email', {
								rules: [
									{ required: true, message: 'Introduza o seu email' },
									{ min: 8, message: 'O email tem que ter no minimo 8 caracteres' }
								]
							})(
								<Input
									disabled={this.props.isLoading ? true : false}
									className="input"
									prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
									placeholder="Email"
								/>
							)}
						</FormItem>
						<FormItem hasFeedback className="form-item">
							{getFieldDecorator('password', {
								rules: [
									{ required: true, message: 'Introduza a sua senha' },
									{ min: 8, message: 'A senha tem que ter no minimo 8 caracteres' }
								]
							})(
								<Input
									disabled={this.props.isLoading ? true : false}
									className="input"
									prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
									placeholder="Senha"
									type="password"
								/>
							)}
						</FormItem>
						<FormItem hasFeedback className="form-item">
							{getFieldDecorator('registrationCode', {
								rules: [
									{ required: true, message: 'Introduza o cdigo de registro' },
									{ min: 1, message: 'O cdigo tem que ter no minimo 8 caracteres' }
								]
							})(
								<Input
									disabled={this.props.isLoading ? true : false}
									className="input"
									prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />}
									placeholder="Cdigo de Registro"
								/>
							)}
						</FormItem>
						<FormItem>
							<div style={{ color: '#f5222d' }}>{this.props.error}</div>
							<Button
								loading={this.props.isLoading ? true : false}
								type="primary"
								htmlType="submit"
								className="login-form-button"
							>
								Registrar
							</Button>
							<div className="register-container">
								<a onClick={this.navigateLogin} className="register-link">
									J tenho conta
								</a>
							</div>
						</FormItem>
					</div>
				</Form>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		isLoading: state.register.isLoading,
		success: state.register.success,
		error: state.register.error,
		authorized: state.authentication.authorized
	};
};

const mapDispatchToProps = {
	registerPostData,
	loginPostData
};

const RegisterPage = Form.create()(Register);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RegisterPage));



